/* Fonts */
@font-face {
    font-family: fira_mono;
    src: url(/static/static/media/fira_mono.bc2445d5.woff2);
}

@font-face {
    font-family: united_sans_light;
    src: url(/static/static/media/united_sans_light.160527a1.woff2);
}

@font-face {
    font-family: united_sans_medium;
    src: url(/static/static/media/united_sans_medium.7e90cb5c.woff2);
}


html, body {
  margin: 0;
  padding: 0;

  font-family: united_sans_light;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100%;
  /*overflow: hidden;*/
}

body {
    background-image: url(/static/static/media/background.d7fea702.jpg);
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover; /* Resize the background image to cover the entire container */

    color: white;    
}

h1, h2, h3, h4 {
    margin-top: 20px;
    margin-bottom: 2px;
}

#root {
    height: 100%;
}
.Login {
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
}


/* login */
#login-form {
    /*display: none;*/
    width: 400px;

    /*background-color: #272743;*/
    background-color: #00000080;

    border: 1px solid cyan;
    border-radius: 10px;

    margin: 0 auto;
    padding: 10px;
}

.Login .login-content {
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;    
}
.NavHeader {
    border-bottom: 1px solid #50505050;
    background-color: rgba(3, 143, 142, 0.3);
    padding: 2px 15px;    
}

.NavHeader .logout {
    float: right;
}

.NavHeader .btn-sm {
    line-height: 0.75;
}

